<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: ['fromLanding'],
    components: {
    },
    setup() {
        
    },
    data: ()=>({
        width: 845
    }),
    computed: {
        frameWidth() {
            return Math.min(Math.min(window.innerWidth, window.screen.availWidth) - 40, 1200);
        },
        frameHeight() {
            return this.frameWidth * 0.5625;
        },
        link() {
            return this.fromLanding ? this.landingLink : this.cabinetLink
        },
        locale() {
            return this.$store.getters.selectedLocale
        },
        cabinetLink() {
            if (this.locale === 'ru-RU') {
                return 'https://www.youtube.com/embed/jV_Cw4lIklQ'
            } else {
                return 'https://www.youtube.com/embed/yVzRFmGFtJc'
            }
        },
        landingLink() {
            if (this.locale === 'ru-RU') {
                return 'https://www.youtube.com/embed/LRvmNfFitlI'
            } else {
                return 'https://www.youtube.com/embed/yy2F6ZBJHw8'
            }
        }
    },
    methods: {
          async close() {
            // console.log('sfwewef')
              this.$emit('close')
          },
          async buyCoin(coinTicker) {
                // if (this.$store.getters.isLoggined === true) {
                //     // await this.$store.dispatch(setBuyCoinTickerToProfile, {coinTicker})
                //     const {scope, email} = await this.$store.dispatch('getProfile')
                //     if (scope === 'not_activated_client') {
                //         this.$router.push('/confirmEmail?email='+email)
                //     } else {
                //         this.$router.push(`/cabinet?buyCoinTicker=${coinTicker}`)
                //     }
                // } else {
                //     this.$router.push(`/login?buyCoinTicker=${coinTicker}`)
                // }
                await this.$store.dispatch('setPaymentCoinTicker', {coinTicker: coinTicker})
                this.$nextTick(()=>{
                    this.$store.dispatch('setShowPaymentCoinModule', {value: true})
                    this.close()
                })
          }
    },
    mounted(){
        this.width = document.getElementById('liqudityVideoModalContentWidthAnchor').offsetWidth;
    }
})
</script>


